import cyrb53 from '@avtopro/cyrb53';

/**
 *   ,      
 * emphasize    -  .
 */
export default class OptionLabelPart {
    constructor(text, emphasize) {
        this.text = text || '';
        this.emphasize = Boolean(emphasize);
    }

    /**
     *        key  React.
     */
    get hashCode() {
        return cyrb53(this.text).toString(16);
    }

    toString() {
        return this.text;
    }
}
