/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import './style/index.less';

/**
 *     
 *      (, ).
 *     Overlay ,
 *         ,
 *     (     ).
 *       "" ( ) Overlay,
 *    .    ""  
 *       . .
 */
export default class Overlay extends React.Component {
    /**
     *    React- Overlay.
     * @param {Object} [props] -  DOM- .
     * @returns {HTMLElement}
     */
    static createContainer(props) {
        if (globalThis.document === undefined) {
            return null;
        }

        const container = document.createElement('div');

        if (props)
            Object.keys(props).forEach((prop) => {
                container[prop] = props[prop];
            });

        Overlay.containers.add(container);

        return container;
    }

    /**
     *    DOM     Overlay.
     * @param {HTMLElement} container
     */
    static deleteContainer(container) {
        if (container && Overlay.containers.has(container)) {
            Overlay.renderContainer(container, { hidden: true });
            Overlay.containers.delete(container);
        }
    }

    /**
     *       
     *   ,    ,      .
     *     ,    React-.
     * @param {HTMLElement} container
     * @param {Object} [props]
     */
    static renderContainer(container, props) {
        if (!container || globalThis.document === undefined) {
            return;
        }
        if (props)
            Object.keys(props).forEach((prop) => {
                container[prop] = props[prop];
            });

        const activeContainers = [...Overlay.containers]
            //     DOM (React-    )
            .map((c) => {
                if (c.parentElement && c.hidden) {
                    c.parentElement.removeChild(c);
                }
                return !c.hidden ? c : null;
            })
            //      
            .filter((c) => c)
            //   ,         
            // "" ( ) Overlay,     
            .map((c, i, all) => {
                const isLast = i == all.length - 1;
                const isMounted = c.parentElement != null;

                //  DOM   ...
                if (isMounted && !isLast) {
                    return c.parentElement.removeChild(c);
                }
                // ...   
                if (!isMounted && isLast) {
                    return document.body.appendChild(c);
                }
                return c;
            })
            //   ,
            //      -    ""
            .filter((c) => c.parentElement != null && c.childElementCount > 0);

        //        Overlay,
        //      body
        if (activeContainers.length > 0) {
            document.body.classList.add('overlay-container');
        } else {
            document.body.classList.remove('overlay-container');
        }
    }

    static propTypes = {
        /**
         *  ,      .
         *   ,   ,   .
         */
        onClose: PropTypes.func,
        /**
         *  .
         */
        onScroll: PropTypes.func,
        /**
         *  onClose      .
         */
        closeOnClick: PropTypes.bool,
        /**
         *  CSS-  .
         */
        className: PropTypes.string,
        /**
         *  ,     DOM.
         */
        hidden: PropTypes.bool,
        /**
         *   .
         */
        children: PropTypes.any.isRequired
    };

    static defaultProps = {
        onClose: null,
        onScroll: null,
        closeOnClick: false,
        className: '',
        hidden: false
    };

    constructor(props) {
        super(props);
        this.container = Overlay.createContainer({ hidden: props.hidden });
    }

    componentDidMount() {
        document.addEventListener('keydown', this.onEscKeyDown);
        Overlay.renderContainer(this.container);
    }

    componentDidUpdate({ hidden: wasHidden }) {
        const { hidden: isHidden } = this.props;
        if (isHidden != wasHidden) {
            Overlay.renderContainer(this.container, { hidden: isHidden });
        }
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.onEscKeyDown);
        Overlay.deleteContainer(this.container);
    }

    static blockName = 'overlay';

    /**
     *   Overlay.
     *    !     .
     */
    static containers = new Set();

    /**
     *    .
     */
    close = () => {
        if (this.props.onClose) {
            this.props.onClose();
        }
    };

    /**
     *     overlay
     *    ,   closeOnClick == true.
     */
    onClick = (e) => {
        const { blockName } = this.constructor;
        if (
            e.target.classList.contains(blockName) &&
            this.props.closeOnClick &&
            this.container.contains(e.target)
        ) {
            this.close();
        }
    };

    /**
     *     Esc,    .
     *         .
     */
    onEscKeyDown = (e) => {
        if (e.keyCode == 27) {
            this.close();
        }
    };

    render() {
        const { hidden, className, onScroll, children } = this.props;
        const { blockName } = this.constructor;

        const overlay = (
            <div
                className={classNames(className, blockName, {
                    [`${blockName}--hidden`]: hidden
                })}
                onClick={this.onClick}
                onScroll={onScroll}
            >
                {children}
            </div>
        );

        //    DOM API   
        //   Overlay    ,
        //        overflow: hidden
        //  ,     Overlay   .
        //  SSR  ,     .
        return this.container
            ? ReactDOM.createPortal(overlay, this.container)
            : overlay;
    }
}
