import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import isMobile from 'is-mobile';
import memoize from 'memoize-one';
import Placeholder from './Placeholder';
import Search from './Search';
import OptionsList from './OptionsList';
import LabelsList from './LabelsList';
import ValuesList from './ValuesList';
import OptionLabelPart from './OptionLabelPart';
import './style/index.less';

/**
 *       .
 * @TODO
 *        
 *  value  query,    undefined.
 * @TODO WAI-ARIA.
 */
export default class Select extends React.Component {
    static propTypes = {
        /**
         *   (,  renderValues == true).
         */
        name: PropTypes.string,
        /**
         *   .
         */
        defaultValue: PropTypes.oneOfType([PropTypes.any, PropTypes.array]),
        /**
         *   ,  defaultValue.
         */
        value: PropTypes.oneOfType([PropTypes.any, PropTypes.array]),
        /**
         *    .
         */
        defaultQuery: PropTypes.string,
        /**
         *    ,  defaultQuery.
         */
        query: PropTypes.string,
        /**
         * ,             .
         */
        placeholder: PropTypes.string,
        /**
         *  CSS-   .
         */
        blockName: PropTypes.string,
        /**
         *  CSS-  
         */
        className: PropTypes.string,
        /**
         *   .
         */
        blockSize: PropTypes.oneOf(['', 'sm']),
        /**
         *  " ",       .
         */
        isMobile: PropTypes.bool,
        /**
         *     .
         */
        framed: PropTypes.bool,
        /**
         *         .
         */
        dropdown: PropTypes.bool,
        /**
         *    .
         */
        multiple: PropTypes.bool,
        /**
         *      .
         */
        searchable: PropTypes.bool,
        /**
         *  ,    .
         */
        disabled: PropTypes.bool,
        /**
         *       .
         */
        renderValues: PropTypes.bool,
        /**
         *         onChange,
         *    ,    .
         */
        acceptsUserOption: PropTypes.bool,
        /**
         *    .  false,  
         *      ,   .
         */
        showGroupNames: PropTypes.bool,
        /**
         *    .
         */
        onChange: PropTypes.func,
        /**
         *     ( ).
         */
        onQueryChange: PropTypes.func,
        /**
         *     :
         *    "focus", "open", "active"
         * onStateChange(<string>eventName, <any>eventValue).
         */
        onStateChange: PropTypes.func,
        /**
         *  .
         */
        children: PropTypes.any,
        /**
         *    .
         */
        toggleIcon: PropTypes.element,
        /**
         *     .
         *     .
         */
        toggleIconSize: PropTypes.oneOf(['normal', 'small']),
        /**
         *    .
         *    100%  .
         */
        dropdownMinWidth: PropTypes.oneOfType([
            PropTypes.number,
            PropTypes.string
        ]),
        /**
         *     .
         */
        dropdownPosition: PropTypes.oneOf(['auto', 'left', 'right']),
        /**
         *  ,       ,
         *     .
         */
        visibleOptionsCount: PropTypes.number,
        /**
         *       
         */
        renderLabels: PropTypes.bool
    };

    static defaultProps = {
        name: '',
        defaultValue: undefined,
        value: undefined,
        defaultQuery: undefined,
        query: undefined,
        placeholder: undefined,
        blockName: 'pro-select',
        className: '',
        blockSize: '',
        isMobile: undefined,
        framed: true,
        dropdown: true,
        multiple: false,
        searchable: false,
        disabled: false,
        renderValues: false,
        acceptsUserOption: false,
        showGroupNames: true,
        onChange: undefined,
        onQueryChange: undefined,
        onStateChange: undefined,
        children: undefined,
        toggleIcon: undefined,
        toggleIconSize: 'normal',
        dropdownMinWidth: undefined,
        dropdownPosition: 'auto',
        visibleOptionsCount: undefined,
        renderLabels: true
    };

    /* eslint-disable react/sort-comp */

    /**
     * @see https://reactjs.org/docs/react-component.html#static-getderivedstatefromprops
     */
    static getDerivedStateFromProps(props) {
        const newState = {};

        if (props.query !== undefined) {
            newState.query = Select.parseQuery(props.query);
        }

        if (props.value !== undefined) {
            newState.selectedValues = Select.parseValue(props.value);
        }

        return Object.keys(newState).length > 0 ? newState : null;
    }

    /**
     *       query.
     * @param {*} value
     * @returns {string}
     */
    static parseQuery = memoize((value) => (value ? value.toString() : ''));

    /**
     *        selectedValues.
     * @param {string|Array} value
     * @returns {Set}
     */
    static parseValue = (value) =>
        value != null
            ? new Set(Array.isArray(value) ? value : [value])
            : new Set();

    /**
     *   RegExp   .
     * @param {string} value -  
     * @param {string} [flags] -   
     * @returns {RegExp}
     */
    static makeQueryRegExp = memoize((value, flags = 'i') => {
        const safeQuery = value.replace(/[.*+?^${}()|[\]\\]/g, '\\$&');
        return new RegExp(`(${safeQuery})`, flags);
    });

    constructor(props) {
        super(props);

        this.state = {
            isFocused: false, //     
            isOpened: false, //   
            isActive: false, //     query
            query: Select.parseQuery(props.defaultQuery),
            selectedValues: Select.parseValue(props.defaultValue)
        };

        this.containerRef = React.createRef();
        this.queryInputRef = React.createRef();
    }

    componentDidMount() {
        document.addEventListener('keydown', this.onKeyDown);
    }

    componentDidUpdate(prevProps, prevState) {
        if (this.props.searchable) {
            const container = this.containerRef.current;
            const queryInput = this.queryInputRef.current;
            //      .
            //   autoFocus,    ,   ,
            //         this.container == null
            if (this.state.isActive && queryInput) {
                queryInput.focus();
            }
            //        ,
            //           onBlur
            //   prevState.isActive  ,     
            else if (prevState.isActive && this.state.isFocused && container) {
                container.focus();
            }
        }

        //    ,     (query)
        //    , .  onOptionChange.
        //           
        //       ,   .
        //    ,  value,  
        //      query,   
        //   ,  query  .
        if (
            !this.props.multiple &&
            prevProps.value != this.props.value &&
            this.state.query != '' &&
            !this.props.query
        ) {
            // eslint-disable-next-line react/no-did-update-set-state
            this.setState({
                query: ''
            });
        }

        if (this.props.onStateChange) {
            const { isFocused, isOpened, isActive } = this.state;
            const {
                isFocused: wasFocused,
                isOpened: wasOpened,
                isActive: wasActive
            } = prevState;

            if (isFocused !== wasFocused)
                this.props.onStateChange('focus', isFocused);
            if (isOpened !== wasOpened)
                this.props.onStateChange('open', isOpened);
            if (isActive !== wasActive)
                this.props.onStateChange('active', isActive);
        }
    }

    componentWillUnmount() {
        document.removeEventListener('keydown', this.onKeyDown);
    }

    get isSearchEnabled() {
        const { searchable: isSearchable } = this.props;
        const { isActive, isFocused } = this.state;
        return isSearchable && isActive && isFocused;
    }

    get isMobileEnabled() {
        const { isMobile: isMobileProp } = this.props;
        return isMobileProp === undefined ? isMobile() : isMobileProp;
    }

    get isDropdownEnabled() {
        const { dropdown } = this.props;
        //        ,
        //     
        return !this.isMobileEnabled && dropdown;
    }

    get options() {
        //      ,
        //     options    
        return this.mapOptions(this.props.children);
    }

    /**
     *   :    ,
     *   label  value  .
     *          this.options!
     * @param {*} children -   Select 
     */
    mapOptions = memoize((childOptions) => {
        const options = React.Children.map(childOptions, (child) => {
            //     
            if (React.isValidElement(child) == false) return null;

            const {
                label: rawLabel,
                value: rawValue,
                group,
                children
            } = child.props;

            const label =
                [
                    rawLabel,
                    typeof children == 'string' ? children : '',
                    rawValue
                ]
                    .find((v) => v)
                    ?.toString() ?? '';

            //  null    
            const value = rawValue !== undefined ? rawValue : label;

            return React.cloneElement(child, {
                label,
                value,
                group
            });
        });

        return (options || []).filter((o) => o);
    });

    /**
     *     ,     .
     * ,     ,   emphasize = true
     *        Option.
     * @param {string} label -  ,    
     * @returns {OptionLabelPart[]} -  ,  null,   
     */
    emphasizeOption = (label) => {
        const { searchable } = this.props;
        const { query } = this.state;

        if (searchable && query && label) {
            const queryRegExp = Select.makeQueryRegExp(query, 'gi');
            return label
                .split(queryRegExp)
                .filter((text) => text)
                .map(
                    (text) => new OptionLabelPart(text, queryRegExp.test(text))
                );
        }
        if (searchable && label) {
            return [new OptionLabelPart(label)];
        }
        if (searchable) {
            return [];
        }
        return null;
    };

    /**
     *  ,    ,
     *   , label   "*".
     */
    getMatchingOptions() {
        const queryRegExp = Select.makeQueryRegExp(this.state.query);
        return this.options.filter((option) => {
            const { label } = option.props;
            return label == '*' || queryRegExp.test(label);
        });
    }

    /**
     *   .
     */
    getSelectedOptions() {
        return this.options.filter((option) =>
            this.state.selectedValues.has(option.props.value)
        );
    }

    /**
     *  ,   
     *    (query),
     *      
     *     .
     */
    applyMatchingOption() {
        const { query } = this.state;
        const { name, multiple, acceptsUserOption } = this.props;
        const options = this.getMatchingOptions();

        if (query && acceptsUserOption) {
            this.props.onChange?.(name, [query]);
            return;
        }

        //    
        //     .
        //      .
        if (query && !multiple && options.length == 1) {
            this.onOptionChange(options[0]);
        }
    }

    /**
     *    .
     *     -  
     *      .
     */
    onQueryChange = (e) => {
        const { onQueryChange, disabled } = this.props;

        if (disabled) {
            return;
        }

        this.setState({
            query: e.target.value,
            isOpened: true
        });

        if (onQueryChange) {
            onQueryChange(e.target.value);
        }
    };

    /**
     *    .
     * @param {Object} option
     * @param {boolean} [single] -  true,      ,
     *                                
     */
    onOptionChange = (option, single) => {
        const { value } = option?.props || {};
        const { multiple, disabled } = this.props;

        if (disabled) {
            return;
        }

        const selectedValues =
            single || !multiple
                ? Select.parseValue(null)
                : Select.parseValue([...this.state.selectedValues]);

        //   ,   
        //    0, null, false   
        if (value !== undefined) {
            //        
            //    .     ,
            //      .
            if (multiple && selectedValues.has(value)) {
                selectedValues.delete(value);
            } else {
                selectedValues.add(value);
            }
        }

        this.setState({
            query: '',
            selectedValues,
            isActive: false,
            isOpened: single ? this.state.isOpened : multiple == true
        });

        if (this.props.onChange) {
            this.props.onChange(this.props.name, [...selectedValues]);
        }
    };

    /**
     * ,       .
     * @todo    .
     * @todo   "activate".
     */
    active = () => {
        this.setState({
            isActive: true,
            isOpened: true,
            isFocused: true
        });
    };

    open() {
        this.setState({
            isOpened: true
        });
    }

    close() {
        this.setState({
            isActive: false,
            isOpened: false
        });
    }

    focus() {
        if (this.state.isFocused == false) {
            this.setState({
                isFocused: true,
                isActive: this.state.query != ''
            });
        }
    }

    /**
     *    .
     */
    onFocus = () => {
        this.focus();
    };

    /**
     *     .
     *   ,      .
     *  ,      onBlur  onFocus.
     */
    onInputFocus = (e) => {
        if (this.state.isFocused) {
            e.stopPropagation();
        }
    };

    /**
     *    ,  
     *     .
     */
    onClick = () => {
        const isSearchable = this.props.searchable;
        this.setState({
            isActive: isSearchable,
            isFocused: true,
            isOpened: isSearchable ? true : !this.state.isOpened
        });
    };

    /**
     *  / .
     *  ,    onClick.
     */
    onToggleClick = (e) => {
        e.stopPropagation();
        this.setState({
            isFocused: true,
            isOpened: !this.state.isOpened
        });
    };

    /**
     *      -    .
     *       .
     */
    onBlur = (e) => {
        const rt = e.relatedTarget || (document && document.activeElement);
        const { query } = this.state;
        const { value } = this.props;

        if (rt === null || (rt && e.currentTarget.contains(rt) == false)) {
            if (query != value) {
                this.applyMatchingOption();
            }
            this.setState({
                isFocused: false,
                isActive: false,
                isOpened: false
            });
        }
    };

    /**
     *        Overlay.
     */
    onClose = () => {
        this.close();
    };

    /**
     *     (   ).
     */
    onClear = () => {
        this.onOptionChange(null, true);
    };

    onKeyDown = (e) => {
        if (this.state.isFocused && e.target == this.containerRef.current) {
            const { isActive, isOpened } = this.state;

            switch (e.keyCode) {
                // Enter:
                // -         -  ;
                // -   ,    -   ;
                // -      - ;
                // -      -    .
                case 13:
                    if (isActive) {
                        this.applyMatchingOption();
                    } else {
                        this.setState({
                            isActive: !isOpened, //   !
                            isOpened: !isOpened
                        });
                    }
                    break;

                // Esc:  
                case 27:
                    this.setState({
                        isActive: false,
                        isOpened: false
                    });
                    break;

                // Backspace:      query
                //   , backspace   
                case 8:
                    if (!isActive && this.state.query) {
                        this.setState({
                            query: '',
                            isActive: true
                        });
                    }
                    break;

                // Space:       
                //   , space   
                case 32:
                    if (!isActive) {
                        e.preventDefault();
                        this.setState({
                            isOpened: true
                        });
                    }
                    break;

                //   -   -  ,
                //     ,   .
                //     -   code  charCode,
                //   SyntheticEvent  keydown  -  ,
                //    : https://developer.mozilla.org/ru/docs/Web/Events/keydown
                //
                // (!)       "keydown",
                //       .   null .
                default:
                    if (e.key?.length == 1 && !isActive) {
                        this.setState({
                            query: '',
                            isActive: true
                        });
                    }
            }
        }
    };

    render() {
        const {
            name,
            framed,
            blockName,
            className,
            blockSize,
            placeholder,
            multiple,
            disabled,
            showGroupNames,
            renderValues,
            toggleIcon,
            toggleIconSize,
            dropdownMinWidth,
            dropdownPosition,
            visibleOptionsCount,
            renderLabels
        } = this.props;
        const { selectedValues, isFocused, isOpened, query } = this.state;
        const options = this.getMatchingOptions();
        const selectedOptions = this.getSelectedOptions();

        const resultClassName = classNames(className, {
            [blockName]: true,
            [`${blockName}--${blockSize}`]: blockSize,
            [`${blockName}--framed`]: framed,
            [`${blockName}--disabled`]: disabled,
            [`${blockName}--opened`]: isOpened && options.length > 0,
            [`${blockName}--focused`]: isFocused,
            [`${blockName}--mobile`]: this.isMobileEnabled
        });

        return (
            <div
                className={resultClassName}
                onFocus={this.onFocus}
                onBlur={this.onBlur}
                /* eslint-disable-next-line jsx-a11y/no-noninteractive-tabindex */
                tabIndex="0"
                data-value={[...selectedValues].join(';')}
                data-name={this.props.name}
                ref={this.containerRef}
            >
                <Placeholder
                    onChange={this.onOptionChange}
                    onClick={this.onClick}
                    onToggleClick={this.onToggleClick}
                    placeholder={placeholder}
                    disabled={disabled}
                    multiple={multiple}
                    withLabels={multiple && !isOpened && renderLabels}
                    options={options}
                    selectedOptions={selectedOptions}
                    blockName={blockName}
                    blockSize={blockSize}
                    toggleIcon={toggleIcon}
                    toggleIconSize={toggleIconSize}
                >
                    {this.isSearchEnabled ? (
                        <Search
                            ref={this.queryInputRef}
                            value={query}
                            onChange={this.onQueryChange}
                            onFocus={this.onInputFocus}
                            blockName={blockName}
                        />
                    ) : null}
                </Placeholder>
                {isOpened && !disabled ? (
                    <OptionsList
                        blockName={blockName}
                        blockSize={blockSize}
                        options={options}
                        multiple={multiple}
                        dropdown={this.isDropdownEnabled}
                        dropdownMinWidth={dropdownMinWidth}
                        dropdownPosition={dropdownPosition}
                        showGroupNames={showGroupNames}
                        isFocused={isFocused}
                        selectedValues={selectedValues}
                        emphasizeOption={this.emphasizeOption}
                        visibleOptionsCount={visibleOptionsCount}
                        onChange={this.onOptionChange}
                    >
                        {multiple && renderLabels ? (
                            <LabelsList
                                blockName={blockName}
                                blockSize={blockSize}
                                options={[...selectedOptions]}
                                onChange={this.onOptionChange}
                                onClear={this.onClear}
                            />
                        ) : null}
                    </OptionsList>
                ) : null}
                {renderValues ? (
                    <ValuesList
                        name={name}
                        values={[...selectedValues]}
                        options={this.options}
                    />
                ) : null}
            </div>
        );
    }
}
