import React, { useState, useEffect, useMemo } from 'react';
import classNames from 'classnames';
import PropTypes from 'prop-types';
import Overlay from '@avtopro/overlay';
import Preloader from '@avtopro/preloader';
import ModalHead from './ModalHead';
import ModalClose from './ModalClose';
import './style/modwin.less';

/**
 *   .
 */
export default function Modal({
    id,
    mode,
    size,
    children,
    paddingless,
    blockName,
    className,
    disabled,
    hidden,
    onClose,
    onScroll,
    closeOnClick
}) {
    const sizeValue = useMemo(
        () => ({
            custom: typeof size == 'number' && size >= 0 ? size : null,
            preset: typeof size == 'string' ? size : null
        }),
        [size]
    );
    const styleProp = useMemo(
        () => (sizeValue.custom ? { width: sizeValue.custom } : null),
        [size]
    );

    //      ,  size="fullscreen",
    //         ,
    //     size     .
    const [forceFullscreen, setFullscreen] = useState(
        sizeValue.preset == 'fullscreen'
    );

    useEffect(() => {
        if (sizeValue.custom == null) return undefined;
        try {
            const mql = window.matchMedia(`(max-width: ${sizeValue.custom}px)`);
            const mqlListener = (e) => {
                setFullscreen(e.matches);
            };

            setFullscreen(mql.matches);

            if (mql.addEventListener) {
                mql.addEventListener('change', mqlListener);
                return () => {
                    mql.removeEventListener('change', mqlListener);
                };
            }
            //  addListener  removeListener ,
            //      Safari <= 12.
            if (mql.addListener) {
                mql.addListener(mqlListener);
                return () => {
                    mql.removeListener(mqlListener);
                };
            }

            throw new Error('MediaQueryList is not supported.');
        } catch (e) {
            //  MediaQueryList   (  ),
            //       ,  .
            setFullscreen(true);
            return undefined;
        }
    }, [size]);

    return (
        <Overlay
            hidden={hidden}
            onClose={onClose}
            onScroll={onScroll}
            closeOnClick={closeOnClick}
        >
            <div
                id={id}
                style={styleProp}
                className={classNames({
                    [className]: true,
                    [blockName]: true,
                    [`${blockName}--${sizeValue.preset}`]:
                        sizeValue.preset && !forceFullscreen,
                    [`${blockName}--fullscreen`]: forceFullscreen,
                    [`${blockName}--no-pad`]: paddingless
                })}
            >
                {!disabled ? null : <Preloader fixed />}
                <div className={`${blockName}__content`}>
                    <ModalHead mode={mode} />
                    {children}
                </div>
                {onClose && !disabled ? <ModalClose onClick={onClose} /> : null}
            </div>
        </Overlay>
    );
}

Modal.propTypes = {
    /**
     *   .
     */
    id: PropTypes.string,
    /**
     *       .
     */
    mode: PropTypes.oneOf(['info', 'warning', 'error', 'success']),
    /**
     *   :   ,  .
     */
    size: PropTypes.oneOfType([
        PropTypes.number,
        PropTypes.oneOf(['wide', 'wider', 'fullscreen'])
    ]),
    /**
     *      .
     */
    paddingless: PropTypes.bool,
    /**
     *  CSS-.
     */
    blockName: PropTypes.string,
    /**
     *  CSS-  .
     */
    className: PropTypes.string,
    /**
     *  ,  .
     */
    disabled: PropTypes.bool,
    /**
     *      ,
     *     .
     */
    hidden: PropTypes.bool,
    /**
     *   ,     Esc,  -.
     * @TODO       Overlay.
     */
    onClose: PropTypes.func,
    /**
     *     .
     */
    onScroll: PropTypes.func,
    /**
     *   .
     */
    children: PropTypes.node,
    /**
     *  onClose      .
     */
    closeOnClick: PropTypes.bool
};

Modal.defaultProps = {
    id: null,
    mode: null,
    size: null,
    paddingless: false,
    blockName: 'modwin',
    className: '',
    disabled: false,
    hidden: false,
    onClose: null,
    onScroll: null,
    children: null,
    closeOnClick: false
};
