import React, { useLayoutEffect } from 'react';
import PropTypes from 'prop-types';
import DataList from './DataList';

const OptionsList = ({
    options,
    blockName,
    blockSize,
    multiple,
    dropdown,
    dropdownMinWidth,
    dropdownPosition,
    showGroupNames,
    isFocused,
    selectedValues,
    onChange,
    emphasizeOption,
    visibleOptionsCount,
    children
}) => {
    const wrapperRef = React.createRef();
    const listRef = React.createRef();

    //    ,
    //      visibleOptionsCount 
    useLayoutEffect(
        () => {
            const { firstLi, topLi, bottomLi } = listRef.current?.liRefs || {};
            const wrapper = wrapperRef.current;

            const getBoundingClientRect = (el, prop) =>
                el?.getBoundingClientRect()[prop] || 0;

            //   
            const wrapperTop = getBoundingClientRect(wrapper, 'top');
            //      (   )
            const bottom = getBoundingClientRect(bottomLi, 'bottom');
            //      (     
            //  ,        ,   )
            const top = getBoundingClientRect(topLi, 'top');
            //          ,    
            //   ,       
            const padding =
                topLi == firstLi
                    ? Math.max(
                          getBoundingClientRect(firstLi, 'top') - wrapperTop
                      )
                    : 0;

            const height = bottom - top + padding;

            if (height > 0 && wrapper) {
                wrapper.style.maxHeight = `${height}px`;
            }
        },
        //        "  / -",
        //         (   )
        //     
        [selectedValues.size > 0]
    );

    //       
    useLayoutEffect(() => {
        const selectedLi = listRef.current?.liRefs.selectedLi;
        const wrapper = wrapperRef.current;

        //       ,
        //          ,
        //         
        if (selectedLi && wrapper && !multiple) {
            const {
                top: liTop,
                bottom: liBottom
            } = selectedLi.getBoundingClientRect();
            const {
                top: wrapperTop,
                bottom: wrapperBottom
            } = wrapper.getBoundingClientRect();

            //        -
            //  ,      
            if (wrapperTop > liTop) {
                wrapper.scrollTo({
                    top: wrapper.scrollTop - (wrapperTop - liTop),
                    behavior: 'instant'
                });
            }
            // ,       
            else if (wrapperBottom < liBottom) {
                wrapper.scrollTo({
                    top: wrapper.scrollTop + (liBottom - wrapperBottom),
                    behavior: 'instant'
                });
            }
        }
    }, [multiple ? selectedValues.size > 0 : selectedValues]);

    return (
        <div
            style={
                dropdown
                    ? {
                          minWidth: dropdownMinWidth,
                          left: dropdownPosition == 'right' ? 'auto' : null,
                          right: dropdownPosition == 'left' ? 'auto' : null
                      }
                    : null
            }
            className={
                dropdown
                    ? `${blockName}__options ${blockName}__options--dropdown`
                    : `${blockName}__options`
            }
        >
            <div className={`${blockName}__options__bg`}>
                {children}
                <div
                    ref={wrapperRef}
                    className={`${blockName}__options__overflow`}
                >
                    <DataList
                        ref={listRef}
                        blockSize={blockSize}
                        isFocused={isFocused}
                        onChange={onChange}
                        selectedValues={selectedValues}
                        options={options}
                        multiple={multiple}
                        showGroupNames={showGroupNames}
                        emphasizeOption={emphasizeOption}
                        visibleOptionsCount={visibleOptionsCount}
                    />
                </div>
            </div>
        </div>
    );
};

OptionsList.propTypes = {
    /**
     *  CSS- 
     */
    blockName: PropTypes.string,
    /**
     *  React-,     
     *       value    selectedValues
     */
    options: PropTypes.arrayOf(PropTypes.object).isRequired,
    /**
     *  Set    
     */
    selectedValues: PropTypes.object.isRequired,
    /**
     *    
     *  false,        
     */
    showGroupNames: PropTypes.bool,
    /**
     *     ,    " "
     */
    isFocused: PropTypes.bool,
    /**
     *   
     */
    onChange: PropTypes.func,
    /**
     *    
     *      
     *  ,        
     */
    multiple: PropTypes.bool,
    /**
     *         .
     */
    dropdown: PropTypes.bool,
    /**
     *    .
     *    100%  .
     */
    dropdownMinWidth: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    /**
     *     .
     */
    dropdownPosition: PropTypes.oneOf(['auto', 'left', 'right']),
    /**
     *        
     *    ,      (query)
     */
    emphasizeOption: PropTypes.func,
    /**
     *  ,       ,
     *     
     */
    visibleOptionsCount: PropTypes.number,
    children: PropTypes.node
};

export default OptionsList;
